.form__checkbox-label {
  position: relative;
  cursor: pointer;
  display: flex;
}
@media (max-width: 767px) {
  .form__checkbox-label {
    margin-right: auto;
  }
}

.form__checkbox-label:hover .form__checkbox-text {
  color: rgba(255, 255, 255, 0.5);
}

.form__checkbox-label:hover .form__checkbox-checkmark {
  background-color: rgba(255, 255, 255, 0.5);
}

.form__checkbox-label input {
  position: absolute;
  opacity: 0;
  height: 0;
  width: 0;
}

.form__checkbox-label input:checked ~ .form__checkbox-checkmark {
  background-image: url("../../img/checkbox-mark.svg");
  background-position: center;
  background-repeat: no-repeat;
}

.form__checkbox-checkmark {
  position: absolute;
  display: block;
  box-sizing: border-box;
  top: 5px;
  width: 18px;
  height: 18px;
  background-color: #f9fbfd;
}

.form__checkbox-text {
  display: block;
  font-size: 10px;
  line-height: 13px;
  font-family: "Montserrat", Arial, sans-serif;
  padding-left: 27px;
}

